import { Selection } from '@recogito/recogito-client-core';
import { toSVGTarget } from '../../selectors/EmbeddedSVG';
import { SVG_NAMESPACE } from '../../util/SVG';
import Mask from './PolygonMask';

export default class RubberbandPolygon {

  constructor(anchor, g, env) {
    console.log("env of RubberbandPolygon", env);
    this.points = [];
    this.points.push([ anchor, anchor ])
    // console.log("points initiated:", this.points);
    this.env = env;

    this.group = document.createElementNS(SVG_NAMESPACE, 'g');
    
    this.polygon = document.createElementNS(SVG_NAMESPACE, 'g');
    this.polygon.setAttribute('class', 'a9s-selection');

    this.outer = document.createElementNS(SVG_NAMESPACE, 'path');
    this.outer.setAttribute('class', 'a9s-outer');

    this.inner = document.createElementNS(SVG_NAMESPACE, 'path');
    this.inner.setAttribute('class', 'a9s-inner');

    this.setPoints(this.points);

    this.mask = new Mask(env.image, this.inner);

    this.polygon.appendChild(this.outer);
    this.polygon.appendChild(this.inner);

    // Additionally, selection remains hidden until 
    // the user actually moves the mouse
    this.group.style.display = 'none';

    this.group.appendChild(this.mask.element);
    this.group.appendChild(this.polygon);

    g.appendChild(this.group);
  }

  setPoints = points => {
    var attr ="";
    for (var ps of points){
      var attr2=""
      if (ps.length>0){
        for (var p of ps) {
          if (p){
            if (attr2===""){
              attr2  +=`M${p[0]},${p[1]}`;
            }
            else{
              attr2  +=` L${p[0]},${p[1]}`;
            }
          }
        };
         attr+=attr2
      }
    }
    attr+=" Z"
    this.outer.setAttribute('d', attr);
    this.inner.setAttribute('d', attr);
  }

  getBoundingClientRect = () =>
    this.outer.getBoundingClientRect();

  dragTo = xy => {
    // Make visible
    this.group.style.display = null;
    const head = this.points[this.points.length - 1].slice(0, this.points[this.points.length - 1].length - 1);
    var headRest=this.points.slice(0,-1)
    const rubberband = [ ...head, xy, head[0] ];
    headRest.push(rubberband)
    this.setPoints(headRest);
    this.mask.redraw();
  }

  addPoint = xy => {
    // Don't add a new point if distance < 2 pixels
    // console.log("Entering addpoint", this.points[this.points.length - 1].length);
    if (this.points[this.points.length - 1].length>0){
      const head = this.points[this.points.length - 1].slice(0, this.points[this.points.length - 1].length - 1);
      const lastCorner = head[head.length - 1];
      const dist = Math.pow(xy[0] - lastCorner[0], 2) + Math.pow(xy[1] - lastCorner[1], 2);
      if (dist > 4) {
        this.points[this.points.length - 1] = [ ...head, xy, head[0] ];
        this.setPoints(this.points);   
        this.mask.redraw();
      } 
    } else{
      this.points[this.points.length - 1] = [xy,xy];
      // console.log(this.points[this.points.length - 1]);
      this.setPoints(this.points);
    }
  }

  get element() {
    return this.polygon;
  }
  undo = () => {
    console.log("last",this.points[this.points.length - 1].length);
    if (this.points[this.points.length - 1].length>2){
      this.points[this.points.length - 1].pop();
    } else {
      if (this.points.length>1){
        this.points.pop()
      }
    }
  }
  newPart = () => {
    // console.log("NewPart triggered");
    this.points.push([]);
    // console.log("points after newPart: ", this.points);
  }


  destroy = () => {
    this.group.parentNode.removeChild(this.group);
    this.polygon = null;    
    this.group = null;
  }

  toSelection = () => {
    // console.log("to selection triggered", this.group, this.env.image);
    return new Selection(toSVGTarget(this.group, this.env.image));
  }

}
